    presentation: "Le collectif Connexion Limitée / Limited Connection est né d’une rencontre transatlantique entre Providence (Etats-Unis) et Paris (France) en 2019, et d’un désir commun de jeunes poètes de contribuer à ouvrir le champ poétique étatsunien contemporain à la France et réciproquement. Nous sommes français.e.s et américain.e.s, avons vécu de part et d'autre de l'Atlantique, étudié et pratiqué l’art et la littérature. Nous représentons des identités raciales et sexuelles diverses. Des valeurs nous rassemblent, au-delà de notre passion commune pour la poésie et le travail de la langue ; parmi elles, l’attention particulière à faire connaître par nos traductions des poètes qui, pour des raisons économiques ou politiques, nous paraissent moins bien visibilisé.e.s par le monde éditorial. Nous avons également soin de travailler en accord et en dialogue avec les auteur.rices que nous avons choisi de traduire. La dimension collective de nos traductions est au cœur de notre projet : elle donne lieu à une traduction enrichie par nos savoirs et expériences plurielles.",
